@import "./sprite/sprite";
@import "./config/general";

.service {
  background: url("../img/Symbol-Logo.png") 50% 29% no-repeat #233148;
  padding: 128px 0 245px;
  position: relative;

  @media (max-width: $mobile) {
    padding: 88px 0 245px; }

  &__title {
    margin-bottom: 107px;
    max-width: 350px;

    @media (max-width: $mobile) {
      max-width: 240px;
      margin-bottom: 40px; } }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    max-width: 976px;
    margin-left: auto;
    margin-right: auto; }

  &-item {
    width: 45%;
    margin-bottom: 100px;
    position: relative;

    @media (max-width: $mobile) {
      width: 100%;
      margin-bottom: 39px; }

    &__icon {
      width: 52px;
      margin-bottom: 12px;

      img {
        max-width: 100%; }

      @media (max-width: $mobile) {
        width: 38px;
        margin-bottom: 8px; } }

    &__title {
      font-size: 32px;
      font-family: $bold;
      font-weight: 700;
      margin-bottom: 40px;
      letter-spacing: 1px;

      @media (max-width: $mobile) {
        font-size: 22px;
        letter-spacing: 0.1px;
        margin-bottom: 22px; } }

    &__descrip {
      font-size: 18px;
      color: #8796ae;
      font-family: $light;
      font-weight: 300;
      letter-spacing: 0.5px;
      line-height: 1.5;

      @media (max-width: $mobile) {
        font-size: 16px;
        line-height: 1.6;
        letter-spacing: -0.1px; } } } }

.lightpaper {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);

  &__box {
    position: relative;
    padding-top: 45px;
    z-index: 1;

    &:before {
      content: '';
      position: absolute;
      width: 200%;
      height: 100%;
      top: 0;
      right: 30px;
      background: $blue;
      z-index: -1; }

    &:after {
      background-image: url("../img/Symbol-Docs.png");
      background-position: 30px 71px;
      background-repeat: no-repeat;
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 0; }

    @media (max-width: $laptop) {
      &:before {
        right: 0; } }

    @media (max-width: 1024px) {
      &:after {
        background-position: 20px 150px; } }

    @media (max-width: $padmini) {
      padding-top: 150px;

      &:after {
        background-position: 20px 260px; } }

    @media (max-width: $mobile) {
      padding-top: 202px;

      &:after {
        background-position: 0 170px; } } }

  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    max-width: 585px;
    margin-left: 0;
    margin-right: auto;
    position: relative;
    z-index: 1;

    @media (max-width: 1024px) {
      flex-direction: column; } }

  &__title {
    font-size: 32px;
    font-family: $xbold;
    font-weight: 700;
    letter-spacing: 1px;
    max-width: 294px;
    padding-right: 15px;
    line-height: 1.2;
    padding-top: 20px;

    @media (max-width: $mobile) {
      font-size: 22px;
      max-width: 180px;
      padding-right: 0;
      padding-top: 0; } }

  &__descrip {
    font-size: 24px;
    font-family: $primary;
    font-weight: 100;
    letter-spacing: .6px;
    max-width: 196px;
    margin-top: 55px;
    line-height: 1.45;

    @media (max-width: $mobile) {
      font-size: 16px;
      margin-top: 36px; } }

  &__btn {
    margin-top: 32px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    transform: translateY(50%);

    @media (max-width: $mobile) {
 } }      //margin-top: 0

  &__image {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(3%, -39%);
    display: block;
    z-index: 2;

    @media (max-width: $laptop) {
      right: -12%; }

    @media (max-width: 900px) {
      transform: translate(13%, -48%); }

    @media (max-width: 700px) {
      transform: translate(13%, -48%) scale(0.8); }

    @media (max-width: 630px) {
      transform: translate(22%, -62%) scale(0.8); }

    @media (max-width: $mobile) {
      transform: translate(37%, -56%) scale(0.7); } } }

.search {
  padding-top: 330px;
  padding-bottom: 160px;

  @media (max-width: $mobile) {
    padding-top: 298px; }

  &__title {
    color: $navy;
    max-width: 580px;
    letter-spacing: 1.5px;

    @media (max-width: $mobile) {
      letter-spacing: 1px; } } }

