/*  ========================= Devices ========================= */

$desktop: 1170px;
$laptop:  1150px;
$pad:     992px;
$padmini: 768px;
$mobile:  568px;

/*==============================================================*/
/*=========================== Burger ===========================*/

$burger-width:      24px;
$burger-height:     20px;
$burger-ml:         2rem;
$burger-border:     4px; // Желательно ставить кратную 2, так как если нет то будет расзываться центр
$burger-media-show: $pad - 1px;
$burger-color:      grey;
$burger-hover:      #3a2f64;
$burger-active:     #bbc2cc;

/*==============================================================*/

$sitewidth:      1170px;
$siteInnerwidth: 117rem;
$bg-body:        #eaeff5;
$text:           #ffffff;
$blue:           #1f65d9;
$navy:           #233148;
$yellow:         #fccf18;

@import "fonts";
@import "normalize";
@import "mixing";

// Шрифт по всему сайту будет уменьшаться
// в зависимости от размера экрана

html {
  font-size: 10px;
  @media (max-width: $desktop) {
    font-size: 9.5px; }

  @media (max-width: $laptop) {
    font-size: 9px; }

  @media (max-width: $pad) {
    font-size: 8.5px; }

  @media (max-width: $padmini) {
    font-size: 8px; }

  @media (max-width: $mobile) {
    font-size: 7.5px; }

  @media (min-width: $pad) {
    * {
      -webkit-user-select: text;
      user-select: none; } } }

body {
  box-sizing: border-box;
  font-size: 1.4rem;
  line-height: 1.4;
  color: $text;
  font-family: $primary;
  background: $bg-body;

  *,
  *:after,
  *:before {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    outline: none; }

  [data-aload],
  [data-aload]::before,
  [data-aload]::after {
    background-image: none !important; }

  ul,
  ol {
    padding: 0;
    margin: 0;
    list-style: none; }

  a {
    text-decoration: none; }

  a,
  button,
  input[type="submit"] {
    @include mx-transition(.5s);

    &:hover {
      @include mx-transition(.25s); }

    &:active {
      @include mx-transition(.15s); } }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    font-weight: normal;
    margin: 0; } }

#wrapper {
  max-width: 100%;
  min-width: 320px;
  overflow: hidden;
  min-height: 100vh;
  position: relative;

  .site-content {
    min-height: 40vh;

    &.mr {
      margin-top: 5rem; } } }

.box {
  @include mx-box(); }

.title {
  text-transform: uppercase;
  font-size: 48px;
  padding-top: 40px;
  font-family: $black;
  font-weight: 900;
  line-height: 1.2;
  letter-spacing: 2px;
  color: $text;
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    display: block;
    width: 32px;
    height: 12px;
    background: $blue;
    transform: rotate(-30deg) skewX(-38deg); }

  @media (max-width: $mobile) {
    font-size: 30px;
    padding-top: 39px;

    &:before {
      width: 22px;
      height: 8px;
      top: 6px; } } }

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 390px;
  width: 100%;
  height: 74px;
  background: $yellow;
  color: $navy;
  font-family: $bold;
  font-size: 22px;
  letter-spacing: 1px;
  text-decoration: none;
  outline: none;
  text-transform: uppercase;
  cursor: pointer;
  position: relative;
  border: 3px solid $yellow;
  transition: all 0.5s ease;
  overflow: hidden;

  .layer {
    color: #fff;
    position: absolute;
    top: -74px;
    width: 100%;
    height: 100%;
    left: 0;
    text-transform: uppercase;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: $blue;
    transition: all 0.3s ease; }

  &:hover {
    .layer {
      top: 0; } }

  &:active, &:focus {
    .layer {
      background: darken($blue, 15); } }

  @media (max-width: $mobile) {
    max-width: 280px;
    height: 52px;

    .layer {
      top: -52px; } } }
