/*============================= Mixins that insert to end of class =============================*/

@mixin mx-comp-bg($comp-bg) {
  @if $comp-bg != false {
    background: $comp-bg; } }

@mixin mx-comp-bg-c($comp-bg-c) {
  @if $comp-bg-c != false {
    background-color: $comp-bg-c; } }

@mixin mx-gui-color($gui-color) {
  @if $gui-color != false {
    color: $gui-color; } }

@mixin mx-border($border) {
  @if $border != false {
    border: $border; } }

@mixin mx-border-color($border-color) {
  @if $border-color != false {
    border-color: $border-color; } }

@mixin mx-text-fz($text-fz) {
  @if $text-fz != false {
    font-size: $text-fz; } }

@mixin mx-text-ff($text-ff) {
  @if $text-ff != false {
    font-family: $text-ff; } }

@mixin mx-text-fw($text-fw) {
  @if $text-fw != false {
    font-weight: $text-fw; } }

@mixin mx-text-lts($text-lts) {
  @if $text-lts != false {
    letter-spacing: $text-lts; } }

@mixin mx-text-lh($text-lh) {
  @if $text-lh != false {
    line-height: $text-lh; } }

@mixin mx-text($ff: false,$fz: false,$fw: false,$lts: false,$lh: false) {
  @include mx-text-ff($ff);
  @include mx-text-fz($fz);
  @include mx-text-fw($fw);
  @include mx-text-lts($lts);
  @include mx-text-lh($lh); }

@mixin mx-position($top: auto,$right: auto,$bottom: auto,$left: auto) {
  left: $left;
  top: $top;
  right: $right;
  bottom: $bottom; }

/*==========================================================================================*/

@mixin mx-burger ($width: $burger-width,$height: $burger-height,$color: $burger-color,$color-hover: $burger-hover,$color-active: $burger-active,$border:$burger-border) {
  position: relative;
  width: $width;
  height: $height;
  color: $color;
  cursor: pointer;
  display: block;

  &:hover,
  &:active {
    color: $color-hover; }

  &:before,
  &:after {
    content: ''; }

  &:before,
  &:after,
  div {
    position: absolute;
    left: 50%;
    width: 100%;
    border-top: $border solid; }

  &:before {
    top: 0;
    transform: translateX($s50);
    transition: ease .2s; }

  &:after {
    top: 50%;
    transform: translate($s50, $s50);
    transition: ease .2s; }

  div {
    bottom: 0;
    transform: translateX($s50);
    transition: ease .2s; }

  &.js-active {
    color: $color-active;

    &:hover,
    &:active {
      color: $color-active; }

    &:before {
      top: 50%;
      transform: translateX($s50) rotateZ(45deg);
      transition: ease .3s; }

    &:after {
      transform: translateX($s50) rotateZ(-45deg);
      transition: ease .3s; }

    div {
      transform: translate($s50, 10px);
      opacity: 0;
      transition: ease .3s; }

    &:before,
    &:after,
    div {
      border-top: $border solid; } } }

@mixin mx-box($width: $sitewidth,$width-small: $siteInnerwidth,$padding: 30px) {
  max-width: $width + 2*$padding;
  padding: 0 $padding;
  margin: 0 auto;
  width: 100%;

  &-small {
    max-width: $width-small;
    padding: 0 $padding;
    margin: 0 auto;
    width: 100%; } }

@mixin mx-transition($duration: .3s) {
  transition: ease $duration; }
